/*==================== VARIABLES CSS ====================*/

:root {
  /* CSS VARIABLES FOR LATER US */
  --header-height: 3rem;

  /*========== Colors ==========*/
  /* Change favorite color */
  --hue-color: 210; /*Purple 250 - Green 142 - Blue 230 - Pink 340*/

  /* HSL color mode */
  --first-color: hsl(var(--hue-color), 69%, 61%);
  --first-color-second: hsl(var(--hue-color), 69%, 61%);
  --first-color-alt: hsl(var(--hue-color), 57%, 53%);
  --first-color-lighter: hsl(var(--hue-color), 92%, 85%);
  --first-color-lighter2: hsl(var(--hue-color), 60%, 88%);
  --title-color: hsl(var(--hue-color), 8%, 15%);
  --text-color: hsl(var(--hue-color), 8%, 45%);
  --text-color-light: hsl(var(--hue-color), 8%, 65%);
  --input-color: hsl(var(--hue-color), 70%, 96%);
  --body-color: hsl(var(--hue-color), 60%, 99%);
  --container-color: #fff;
  --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);
  --scroll-thumb-color: hsl(var(--hue-color), 12%, 80%);

  /*========== Font and typography ==========*/
  --body-font: "Poppins", sans-serif;
  /* --body-font: "Ubuntu", sans-serif; */

  /* .5rem = 8px, 1rem = 16px, 1.5rem = 24px ... */
  --big-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --normal-font-size: 0.938rem;
  --small-font-size: 0.813rem;
  --smaller-font-size: 0.75rem;

  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;

  /*========== Margenes Bottom ==========*/
  /* .25rem = 4px, .5rem = 8px, .75rem = 12px ... */
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;

  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

/* Font size for large devices */
@media screen and (min-width: 968px) {
  :root {
    --big-font-size: 3rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: 0.875rem;
    --smaller-font-size: 0.813rem;
  }
}

/*========== Variables Dark theme ==========*/

body.dark-theme {
  /* HSL color mode */
  --first-color-second: hsl(var(--hue-color), 30%, 8%);
  --title-color: hsl(var(--hue-color), 8%, 95%);
  --text-color: hsl(var(--hue-color), 8%, 75%);
  --input-color: hsl(var(--hue-color), 29%, 16%);
  --body-color: hsl(var(--hue-color), 28%, 12%);
  --container-color: hsl(var(--hue-color), 29%, 16%);

  --scroll-bar-color: hsl(var(--hue-color), 12%, 48%);
  --scroll-thumb-color: hsl(var(--hue-color), 12%, 36%);
}

/*==================== BASE ====================*/
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  /*top right bottom left*/
  margin: 0 0 3rem 0;
  margin: 0 0 var(--header-height) 0;
  font-family: "Poppins", sans-serif;
  font-family: var(--body-font);
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  background-color: hsl(210, 60%, 99%);
  background-color: var(--body-color);
  color: hsl(210, 8%, 45%);
  color: var(--text-color);
}

h1,
h2,
h3,
h4 {
  color: hsl(210, 8%, 15%);
  color: var(--title-color);
  font-weight: 600;
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

/*==================== LAYOUT ====================*/

.container {
  max-width: 768px;
  margin-left: 1.5rem;
  margin-left: var(--mb-1-5);
  margin-right: 1.5rem;
  margin-right: var(--mb-1-5);
}

.grid {
  display: grid;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

/*==================== REUSABLE CSS CLASSES ====================*/

.section {
  padding: 2rem 0 4rem;
}

.section__title {
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
}

.section__subtitle {
  display: block;
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  margin-bottom: 2.5rem;
  margin-bottom: var(--mb-2-5);
}

.section__title,
.section__subtitle {
  text-align: center;
}

/*==================== BUTTONS ====================*/

.button {
  display: inline-block;
  background-color: hsl(210, 69%, 61%);
  background-color: var(--first-color);
  color: #fff;
  padding: 1rem;
  border-radius: 0.5rem;
  font-weight: 500;
  font-weight: var(--font-medium);
}

.button2 {
  display: inline-block;
  background-color: hsl(210, 69%, 61%);
  background-color: var(--first-color);
  color: #fff;
  padding: 0.5rem;
  border-radius: 0.5rem;
  font-weight: 500;
  font-weight: var(--font-medium);
  font-size: 0.75rem;
  font-size: var(--smaller-font-size);
  margin-top: 1rem;
  margin-top: var(--mb-1);
  width: 100%;
}

.button:hover {
  background-color: hsl(210, 57%, 53%);
  background-color: var(--first-color-alt);
}

.button__icon {
  font-size: 1.25rem;
  margin-left: 0.25rem;
  margin-left: var(--mb-0-25);
  transition: 0.3s;
}

.button--white {
  background-color: #fff;
  color: hsl(210, 69%, 61%);
  color: var(--first-color);
}

.button--white:hover {
  background-color: #fff;
}

.button--flex {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
}

.button--flex2 {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.button--small {
  padding: 0.75rem 1rem;
}

.button--link {
  padding: 0;
  background-color: transparent;
  color: hsl(210, 69%, 61%);
  color: var(--first-color);
}

.button--link:hover {
  background-color: transparent;
  color: hsl(210, 57%, 53%);
  color: var(--first-color-alt);
}

/*========== SCROLL BAR ==========*/
::-webkit-scrollbar {
  width: 0.6rem;
  background-color: hsl(210, 12%, 90%);
  background-color: var(--scroll-bar-color);
  border-radius: 0.5rem;
}

::-webkit-scrollbar-thumb {
  background-color: hsl(210, 12%, 80%);
  background-color: var(--scroll-thumb-color);
  border-radius: 0.6rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: hsl(210, 8%, 65%);
  background-color: var(--text-color-light);
}

/*==================== MEDIA QUERIES ====================*/
/* For small devices */
@media screen and (max-width: 350px) {
  .container {
    margin-left: 1rem;
    margin-left: var(--mb-1);
    margin-right: 1rem;
    margin-right: var(--mb-1);
  }
}

